.login {
    width: 400px;
    margin: 16px auto;
    font-size: 16px;
}

/* Reset top and bottom margins from certain elements */
.login-header,
.login p {
    margin-top: 0;
    margin-bottom: 0;
}


.login-header {
    background: #28d;
    padding: 20px;
    font-size: 1.4em;
    font-weight: normal;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
}

.login-container {
    background: #ebebeb;
    padding: 12px;
}

/* Every row inside .login-container is defined with p tags */
.login p {
    padding: 12px;
}

.login input {
    box-sizing: border-box;
    display: block;
    width: 100%;
    border-width: 1px;
    border-style: solid;
    padding: 16px;
    outline: 0;
    font-family: inherit;
    font-size: 0.95em;
}

.login input[type="email"],
.login input[type="password"] {
    background: #fff;
    border-color: #bbb;
    color: #555;
}

/* Text fields' focus effect */
.login input[type="email"]:focus,
.login input[type="password"]:focus {
    border-color: #888;
}

.login input[type="submit"] {
    background: #28d;
    border-color: transparent;
    color: #fff;
    cursor: pointer;
}

.login input[type="submit"]:hover {
    background: #17c;
}

/* Buttons' focus effect */
.login input[type="submit"]:focus {
    border-color: #05a;
}

.add-form form {
    display: flex;
    justify-content: space-between;
    /*border: 1px solid white;*/
    max-height: 100px;
    margin: 10px;
    align-items: center;
}

form input {
    padding: 5px;
    margin: 5px;
    color: deepskyblue;
}
form input:not([type = 'file']),select{
    border: 2px solid deepskyblue;
    color: blue;
}

form select {
    padding: 5px 10px;
    margin: 5px;
}
form button {
    padding: 5px 25px;
    border: 2px solid greenyellow;
    width: 100px;
    text-decoration: none;
    color: greenyellow;
    font-weight: bold;
    background: inherit;
    margin: 5px;
}

